Vue.use(Vuex)

const state = {
  // 語系
  language: 'CHS',
  // 題目清單
  questionList: [],
  // 回應給後端的資料格式
  responseList: [],
  companyNum: '',
  clientData: {
    QUEST_REPORT: '',
    COMPANY_NO: '',
    CONTACT_NAME: '',
    CONTACT_TITLE: '',
    MOBILE_PHONE: '',
    COMPANY_NAME: '',
    COMPANY_PHONE: '',
    COMPANY_EMAIL: '',
    EMAIL_CHECK: '',
    CITY_CODE: '',
    CATEGORY_CODE: '',
    CONTACT_TIME: '',
    IMPORT_SYSTEM_TIME: '',
    BUDGET: '',
    REMARK: ''
  }
}

const mutations = {
  updateResponse (state, data) {
    // 第一題如果回答台灣，第二題的選項就必須過濾掉China
    if (state.responseList.length === 0) {
      let checkText
      if (Object.values(data)[0] === 'S150') {
        // 台灣
        checkText = 'TAIWAN'
      } else {
        checkText = 'CHINA'
      }
      state.questionList[1].optionList = state.questionList[1].optionList.filter(element => {
        return element.remark === checkText
      })
    }
    state.responseList.push(data)
  },
  setQuestionList (state, questionObj) {
    let propertyList = Object.keys(questionObj)
    state.questionList = []
    for (let i = 0; i < propertyList.length / 3; i++) {
      state.questionList.push({
        code: null,
        question: null,
        optionList: []
      })
    }

    propertyList.forEach(element => {
      if (element.startsWith('QUEST_CODE')) {
        let questionNo = parseInt(element.split('QUEST_CODE')[1])
        state.questionList[questionNo]['code'] = questionObj[element]
      } else if (element.startsWith('QUESTION')) {
        let questionNo = parseInt(element.split('QUESTION')[1])
        state.questionList[questionNo]['question'] = questionObj[element]
      } else if (element.startsWith('OPTIONLIST')) {
        let questionNo = parseInt(element.split('OPTIONLIST')[1])
        let optionPropertyList = Object.keys(questionObj[element])
        let optionLength = questionNo === 1 ? optionPropertyList.length / 3 : optionPropertyList.length / 2
        for (let j = 0; j < optionLength; j++) {
          state.questionList[questionNo].optionList.push({
            remark: null,
            code: null,
            text: null
          })
        }
        optionPropertyList.forEach(optionObj => {
          if (optionObj.startsWith('OPT_CODE')) {
            let optionNo = parseInt(optionObj.split('OPT_CODE')[1])
            state.questionList[questionNo].optionList[optionNo]['code'] = questionObj[element][optionObj]
          } else if (optionObj.startsWith('TEXT')) {
            let optionNo = parseInt(optionObj.split('TEXT')[1])
            state.questionList[questionNo].optionList[optionNo]['text'] = questionObj[element][optionObj]
          } else if (optionObj.startsWith('REMARK')) {
            let optionNo = parseInt(optionObj.split('REMARK')[1])
            state.questionList[questionNo].optionList[optionNo]['remark'] = questionObj[element][optionObj]
          }
        })
      }
    })
  },
  addCompanyNum (state, data) {
    state.companyNum = data
  },
  setClientData (state, data) {
    state.clientData = data
  }
}

